
'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { KeyRound, ShieldCheck, ShieldAlert, Settings } from 'lucide-react';
import ManageWorks from '@/components/admin/ManageWorks'; // 新增的组件

export default function AdminPage() {
  const [password, setPassword] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // 实际生产环境请务必使用后端验证！
    if (password === '123456') {
      setIsAuthenticated(true);
      setError(null);
    } else {
      setError('密码错误，请重试。');
      setPassword(''); // Clear password field on error
    }
  };

  if (isAuthenticated) {
    return (
      <div className="container mx-auto py-10">
        <header className="mb-8">
          <Card className="shadow-md">
            <CardHeader className="flex flex-row items-center gap-4">
              <ShieldCheck className="w-12 h-12 text-green-500" />
              <div>
                <CardTitle className="text-3xl">管理后台</CardTitle>
                <CardDescription className="text-lg">欢迎来到管理区域。请谨慎操作。</CardDescription>
              </div>
            </CardHeader>
          </Card>
        </header>
        
        <ManageWorks />

        <Card className="mt-12 shadow-lg">
          <CardHeader>
            <CardTitle className="text-2xl flex items-center">
              <Settings className="mr-3 h-6 w-6 text-primary" />
              其他管理功能
            </CardTitle>
            <CardDescription>比赛管理等功能将在此处添加。</CardDescription>
          </CardHeader>
          <CardContent>
            <p className="text-muted-foreground">比赛管理功能正在开发中，敬请期待。</p>
            {/* TODO: 未来在此处添加比赛管理等其他功能 */}
          </CardContent>
        </Card>

      </div>
    );
  }

  return (
    <div className="flex items-center justify-center min-h-[calc(100vh-200px)]">
      <Card className="w-full max-w-md shadow-xl">
        <CardHeader className="text-center">
          <KeyRound className="mx-auto h-12 w-12 text-primary mb-3" />
          <CardTitle className="text-2xl font-bold">管理后台登录</CardTitle>
          <CardDescription>请输入密码以访问管理区域。</CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <Input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="请输入密码"
              className="text-center"
              aria-label="密码"
            />
            {error && (
              <Alert variant="destructive" className="text-sm">
                <ShieldAlert className="h-4 w-4" />
                <AlertTitle>登录失败</AlertTitle>
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
            <Button type="submit" className="w-full">
              登录
            </Button>
          </form>
        </CardContent>
         <CardFooter className="text-xs text-muted-foreground pt-6">
          <p className="text-center w-full">
            <strong>安全提示:</strong> 此处为演示目的使用客户端密码校验，
            <br />
            请勿在实际生产环境中使用此方式保护敏感数据。
          </p>
        </CardFooter>
      </Card>
    </div>
  );
}
