
'use client';

import { useEffect, useState, useTransition } from 'react';
import type { Building, BuildingStatus } from '@/types';
import { getBuildings, updateBuildingStatus } from '@/app/actions';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow, TableCaption } from '@/components/ui/table';
import { useToast } from '@/hooks/use-toast';
import { Loader2, RefreshCw, ShieldAlert, Edit } from 'lucide-react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../ui/card';

const ALL_STATUSES: BuildingStatus[] = ['有效', '等待审核', '违规', '作弊', '无效'];

export default function ManageWorks() {
  const [buildings, setBuildings] = useState<Building[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isUpdating, startUpdateTransition] = useTransition();
  const { toast } = useToast();

  const fetchBuildingsData = async () => {
    setIsLoading(true);
    setError(null);
    try {
      const data = await getBuildings();
      setBuildings(data);
    } catch (err) {
      console.error('Failed to fetch buildings:', err);
      setError('加载作品数据失败，请稍后再试。');
      toast({ title: '错误', description: '加载作品数据失败。', variant: 'destructive' });
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchBuildingsData();
  }, []);

  const handleStatusChange = (buildingId: string, newStatus: BuildingStatus) => {
    startUpdateTransition(async () => {
      const result = await updateBuildingStatus(buildingId, newStatus);
      if (result.success) {
        toast({
          title: '成功',
          description: result.message,
        });
        // Optimistically update local state or refetch
        setBuildings(prevBuildings =>
          prevBuildings.map(b => (b.id === buildingId ? { ...b, status: newStatus } : b))
        );
      } else {
        toast({
          title: '更新失败',
          description: result.message,
          variant: 'destructive',
        });
      }
    });
  };

  if (isLoading) {
    return (
      <div className="flex justify-center items-center py-10">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-3 text-lg">正在加载作品数据...</p>
      </div>
    );
  }

  if (error) {
    return (
      <Card className="my-10 bg-destructive/10 border-destructive">
        <CardHeader>
          <CardTitle className="flex items-center text-destructive">
            <ShieldAlert className="mr-2 h-6 w-6" />
            加载错误
          </CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-destructive-foreground">{error}</p>
          <Button onClick={fetchBuildingsData} variant="destructive" className="mt-4">
            <RefreshCw className="mr-2 h-4 w-4" />
            重试
          </Button>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="mt-6 shadow-lg">
      <CardHeader>
        <CardTitle className="text-2xl flex items-center">
            <Edit className="mr-3 h-6 w-6 text-primary" />
            管理作品状态
        </CardTitle>
        <CardDescription>在此处查看和修改已提交建筑作品的审核状态。</CardDescription>
      </CardHeader>
      <CardContent>
        {buildings.length === 0 ? (
          <p className="text-center text-muted-foreground py-6">暂无作品记录。</p>
        ) : (
          <div className="overflow-x-auto">
            <Table>
              <TableCaption>共 {buildings.length} 个作品。</TableCaption>
              <TableHeader>
                <TableRow>
                  <TableHead className="min-w-[150px]">作品名称</TableHead>
                  <TableHead className="min-w-[100px]">作者</TableHead>
                  <TableHead className="min-w-[120px]">当前状态</TableHead>
                  <TableHead className="min-w-[200px] text-center">修改状态</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {buildings.map(building => (
                  <TableRow key={building.id}>
                    <TableCell className="font-medium">{building.name}</TableCell>
                    <TableCell>{building.author}</TableCell>
                    <TableCell>
                      <span 
                        className={`px-2 py-1 rounded-full text-xs font-semibold
                          ${building.status === '有效' ? 'bg-green-100 text-green-700' : ''}
                          ${building.status === '等待审核' ? 'bg-yellow-100 text-yellow-700' : ''}
                          ${['违规', '作弊', '无效'].includes(building.status) ? 'bg-red-100 text-red-700' : ''}
                        `}
                      >
                        {building.status}
                      </span>
                    </TableCell>
                    <TableCell className="text-center">
                      <div className="flex items-center justify-center space-x-2">
                        <Select
                          defaultValue={building.status}
                          onValueChange={(newStatus) =>
                            handleStatusChange(building.id, newStatus as BuildingStatus)
                          }
                          disabled={isUpdating}
                        >
                          <SelectTrigger className="w-[150px] h-9">
                            <SelectValue placeholder="选择状态" />
                          </SelectTrigger>
                          <SelectContent>
                            {ALL_STATUSES.map(statusValue => (
                              <SelectItem key={statusValue} value={statusValue}>
                                {statusValue}
                              </SelectItem>
                            ))}
                          </SelectContent>
                        </Select>
                        {isUpdating && <Loader2 className="h-4 w-4 animate-spin" />}
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
